.Main {
    padding: 20px;
  }
  
  .hero {
    text-align: center;
    background-color: #e0ffff;
    padding: 50px 20px;
    margin-bottom: 20px;
    border-radius: 8px;
  }
  
  .hero h2 {
    margin: 0 0 10px;
    color: #2f4f4f;
  }
  
  .hero p {
    color: #555;
  }
  
  .hero button {
    padding: 10px 20px;
    background-color: #20b2aa;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .about, .services, .contact {
    margin-bottom: 20px;
  }
  
  h2 {
    color: #2f4f4f;
  }
  
  p {
    color: #555;
  }
  